.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
img{
  width: 400px;
  height: 300px;
}
h1{
  text-align: center;
  font-size: 3rem;
  background-color: yellow;
}
h2{
  text-align: center;
  font-size: 2.5rem;
  color: red;
}
audio{
  width: 400px;
}
video{
  width: 400px;
  height: 300px;
}
.dragonDiv{
  width: 450px;
  height: 2450px;
  display: inline-block;
  border: 5px solid gray;
  text-align: center;
  margin: 10px;
  background-color: aqua;
}
